/*
---
name: Text
category:
 - component/typography/text
---

## 

```html
<p class="c-text"><br></p>

<article class="c-article">
	<p></p>
	<p></p>
</article>
```
*/

.c-text { margin: 10px 0; }

.c-article p { @extend .c-text; }


/*
---
name: highlighted Text 
category:
 - component/typography/text
---

## 

```html
<p class="c-text--highlighted"><br></p>

<article class="c-article">
	<p>
		<strong></strong>
		<br><em></em><em></em>
	</p>
</article>
```
*/

.c-text--highlighted {
	font-style: normal;
	font-weight: bold;
}

.c-article {
	strong, em { @extend .c-text--highlighted; }
}


/*
---
name: align Text 
category:
 - component/typography/text
---

## 

```html
<p class="c-text c-text--align-right"><br></p>
<p class="c-text c-text--align-center"><br></p>
<p class="c-text c-text--align-left"><br></p>

```
*/

.c-text--align-right { text-align: right; }

.c-text--align-center { text-align: center; }

.c-text--align-left { text-align: left; }

