a {
  text-decoration: none;
  color: inherit;

  &,
  &:before,
  &:after,
  * {
    transition: opacity 0.15s linear;
  }

  &:hover {
    opacity: .75;
    cursor: pointer;
    text-decoration: none;

    * {
      opacity: .75;
      text-decoration: none;
    }
  }
}

.c-link__icon {
	@include fz(16);
	vertical-align: 0;
	// 
	&.-front { margin-right: 3px; }
	// 
	&.-rear { margin-left: 5px; }
}
